.house {

    display: flex;
    flex-direction: column;
    &__grid{
    display: grid;
    grid-template-columns: 70% 30%;
    margin-bottom: 2rem;
    }

    &__frame {
        margin: 1rem 2rem 0 0;
        padding: 2rem 1rem 1rem 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: white;
        box-shadow: 0px 4px 35px rgba(0, 0, 0, 0.15);
        border-radius: 5px;
    }

    &__small {
        display: flex;
        justify-content: space-between;
        width: 90%;
        padding: 0.5rem 0 0 0;
    }

    &__img {
        box-sizing: border-box;
        width: 33%;
        padding: 0.5rem;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
        }

        &_big {
            padding: 0;
            width: 90%;
        }
    }

    &__info {
        font-size: 1rem;
        font-weight: normal;
        margin: 1rem 0 0 0;
        padding: 1rem 1.5rem 0 1.5rem;
        background: white;
        box-shadow: 0px 4px 35px rgba(0, 0, 0, 0.15);
        border-radius: 5px;
        >h3{
            margin-top: 2rem;
            margin-bottom: 1rem;
        }
        >table{
            margin-bottom: 1rem;
        }
        >.button{
            width: 100%;
        }
    }

    &__description{
        font-size: 1.2rem;
        padding: 0 1rem;
        margin-top: 1rem;
        margin-bottom: 5rem;
    }
}